import React, { useEffect, useState } from 'react';
import { HashRouter as Router, Routes, Route, Link } from "react-router-dom";
import TopBar from './TopBar';
import ChampionsPage from './ChampionsPage';
import ChampionPage from './ChampionPage';
import SideBar from './SideBar';
import styles from "../index.css";

function App() {
    const [isSideBarOpen, setIsSideBarOpen] = useState(true);
    const [latestPatch, setLatestPatch] = useState("");
    const [selectedChamp, setSelectedChamp] = useState("");   

    useEffect(() => {
        const fetchLatestPatch = async () => {
            const response = await fetch(
                "https://ddragon.leagueoflegends.com/api/versions.json"
            );
            const data = await response.json();
            const patch = data[0];
            setLatestPatch(patch);
        };

        
        fetchLatestPatch();
    }, []);

    

    const toggleSideBar = () => {
        setIsSideBarOpen(!isSideBarOpen);
    };

    return (
        <Router>
            <div>
                <div>
                    <Link id="home" to="/"></Link>
                </div>

                <Routes>
                    <Route
                        exact path="/"
                        element={
                            <React.Fragment>
                                <TopBar toggleSideBar={toggleSideBar} />
                                <ChampionsPage latestPatch={latestPatch} setSelectedChamp={setSelectedChamp} />
                                <SideBar isSideBarOpen={isSideBarOpen} />
                            </React.Fragment>
                        }
                    />
                    <Route
                        path={`/champion`}
                        element={
                            <React.Fragment>
                                <TopBar toggleSideBar={toggleSideBar} />
                                <ChampionPage selectedChamp={selectedChamp} latestPatch={latestPatch} />
                                <SideBar isSideBarOpen={isSideBarOpen} />
                            </React.Fragment>
                        }
                    />
                </Routes>
            </div>
        </Router>

    );
}

export default App;
