import React, { useState } from 'react';

function TopBar({ toggleSideBar }) {
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);
    const [region, setRegion] = useState('TR');

    const toggleDropdown = () => {
        setIsDropdownOpen(!isDropdownOpen);
    };

    const handleRegionClick = (selectedRegion) => {
        setRegion(selectedRegion);
        setIsDropdownOpen(false);
    };

    return (
        <div className="fixed w-full top-0 h-[78px] bg-gray-900 z-50 flex items-center justify-center px-4">
            <div className="fixed left-5 hover:bg-blue-600 duration-300 rounded-md py-1 px-2" onClick={toggleSideBar}>
                <div className="flex row align-middle hover:cursor-pointer group">
                    <i className="bi bi-app-indicator px-2 py-1 bg-blue-600 rounded-md"></i>
                    <h1 className="font-bold group-hover:text-black text-gray-200 duration-500 text-[15px] ml-3 py-1 max-xl:hidden">
                        League Of Statistics
                    </h1>
                </div>
            </div>
            <div className="flex items-center bg-white rounded-md pl-4">
                <i className="bi bi-search text-gray-500"></i>
                <input
                    type="text"
                    placeholder="Summoner Name"
                    className="ml-2 outline-none bg-transparent xl:w-96 max-sm:w-24 max-md:w-36 max-lg:w-64"
                />
                <button className="text-black hover:bg-blue-600 hover:text-gray-100 hover:px-3 duration-300 bg-gray-200 px-2 h-10 rounded-md">
                    <i className="bi bi-binoculars text-gray-500 px-1.5"></i>
                    Search
                </button>
            </div>
            <div className="relative ml-4">
                <button
                    className="text-white hover:bg-blue-600 duration-300 px-2 py-1 h-10 rounded-md hover:px-3"
                    onClick={toggleDropdown}
                >
                    <i className="bi bi-three-dots-vertical"></i>
                    Region
                </button>
                <div className={`absolute  mt-2 w-20 bg-white rounded-md shadow-lg 
                                 ${isDropdownOpen ? 'opacity-100 translate-y-0 duration-500 transition-opacity transform' : 'opacity-0 -translate-y-150 duration-500 transition-opacity transform'}`}>
                        <ul className="py-2 flex flex-col items-center">
                            <li className="px-4 py-2 hover:bg-blue-600 hover:cursor-pointer duration-300 rounded-md" onClick={() => handleRegionClick("TR")}>TR</li>
                            <li className="px-4 py-2 hover:bg-blue-600 hover:cursor-pointer duration-300 rounded-md" onClick={() => handleRegionClick("EU")}>EU</li>
                            <li className="px-4 py-2 hover:bg-blue-600 hover:cursor-pointer duration-300 rounded-md" onClick={() => handleRegionClick("NA")}>NA</li>
                            <li className="px-4 py-2 hover:bg-blue-600 hover:cursor-pointer duration-300 rounded-md" onClick={() => handleRegionClick("KR")}>KR</li>
                        </ul>
                    </div>
            </div>
        </div>
    );
}

export default TopBar;
